.stars {
  display: flex;

  &__star {
    display: flex;
    justify-content: space-between;
    margin-right: 4px;
    width: 16px;
    height: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    flex-shrink: 0;
    }

    &--1 :nth-child(-n + 1),
    &--2 :nth-child(-n + 2),
    &--3 :nth-child(-n + 3),
    &--4 :nth-child(-n + 4),
    &--5 :nth-child(-n + 5) {
      background-image: url(../images/star-active.svg);
    }
}
